<template>
	<el-container>
		<el-header height="40px">
			<el-form :inline="true">
				<el-form-item label="">
					<el-input
						v-model.trim="search.stu_name"
						placeholder=""
						clearable
						prefix-icon="el-icon-search"
						@change="getData()"></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-select v-model="search.stu_cls_id" @change="getData()">
						<i slot="prefix" class="el-icon-search"></i>
						<el-option :value="0" label="-  -"></el-option>
						<el-option :value="null" label="-  -"></el-option>
						<el-option v-for="item in listClass" :key="item.cls_id" :value="item.cls_id" :label="item.cls_name"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="">
					<el-select v-model="search.stu_qualification" @change="getData()">
						<i slot="prefix" class="el-icon-search"></i>
						<el-option :value="0" label="-  -"></el-option>
						<el-option
							v-for="item in $store.getters['dictionary/getGroupByKey']('qualification')"
							:key="item.dic_id"
							:value="item.dic_id"
							:label="item.dic_name"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item style="float: right">
					<el-button type="danger" size="medium" icon="el-icon-download" @click="exportStudent"></el-button>
				</el-form-item>
				<el-form-item style="float: right">
					<el-button type="success" size="medium" icon="el-icon-share" @click="beginAssignClassBatch"></el-button>
				</el-form-item>
				<el-form-item style="float: right">
					<el-button type="primary" size="medium" icon="el-icon-plus" @click="beginAdd"></el-button>
				</el-form-item>
			</el-form>
		</el-header>
		<el-main>
			<!-- elementuitable -->
			<div class="table-wrapper">
				<el-table border stripe :highlight-current-row="true" :data="list" height="100%" @selection-change="selectionChangeHandler">
					<el-table-column type="index" label="#" align="center" fixed></el-table-column>
					<el-table-column type="selection" align="center" fixed ></el-table-column>
					<el-table-column label="" prop="stu_name" width="100" fixed></el-table-column>
					<el-table-column label="" width="120">
						<template slot-scope="{ row }">
							<span style="color: #ccc;" v-if="row.stu_cls_id === null"></span>
							<span v-else v-text="listClass.find(item => item.cls_id === row.stu_cls_id).cls_name"></span>
						</template>
					</el-table-column>
					<el-table-column label="" width="100" align="center">
						<template slot-scope="{ row }">
							<el-popover placement="right" :title="row.stu_name" trigger="click">
								<el-image :src="row.stu_avatar || defaultAvatar" style="width: 200px; height: 280px; margin: 20px 50px;" fit="contain"></el-image>
								<el-button slot="reference" type="text"></el-button>
							</el-popover>
						</template>
					</el-table-column>
					<el-table-column label="" width="60" align="center">
						<template slot-scope="{ row }">
							<span v-text="row.stu_sex === 1 ? '' : ''"></span>
						</template>
					</el-table-column>
					<el-table-column label="" width="120" align="center" prop="stu_phone"></el-table-column>
					<el-table-column label="()" width="140" align="center" prop="stu_phone2"></el-table-column>
					<el-table-column label="" width="120" align="center" prop="stu_born"></el-table-column>
					<el-table-column label="" width="120">
						<template slot-scope="{ row }">
							<span v-text="$store.getters['dictionary/getNameById'](row.stu_qualification)"></span>
						</template>
					</el-table-column>
					<el-table-column label="" width="200" prop="stu_school" show-overflow-tooltip></el-table-column>
					<el-table-column label="" width="200" prop="stu_major" show-overflow-tooltip></el-table-column>
					<el-table-column label="" width="240" prop="stu_address" show-overflow-tooltip></el-table-column>
					<el-table-column label="" width="260" show-overflow-tooltip>
						<template slot-scope="{ row }">
							<span v-if="row.stu_remark === ''" style="color: #ccc;">..</span>
							<span v-else  v-text="row.stu_remark"></span>
						</template>
					</el-table-column>
					<el-table-column label="" width="400" align="center" fixed="right">
						<template slot-scope="{ row }">
							<el-button type="success" size="mini" icon="el-icon-share" @click="beginAssignClass(row)"></el-button>
							<el-button type="danger" size="mini" icon="el-icon-picture" @click="beginUploadAvatar(row)"></el-button>
							<el-button type="primary" size="mini" icon="el-icon-edit" @click="beginUpdate(row)"></el-button>
							<el-button type="warning" size="mini" icon="el-icon-delete"></el-button>
						</template>
					</el-table-column>
				</el-table>
			</div>
		</el-main>
		<el-footer height="32px">
			<el-pagination
				background
				:total="pagination.total"
				:page-size.sync="pagination.pageSize"
				:current-page.sync="pagination.currentPage"
				layout="total, prev, pager, next, jumper, ->, sizes"
				:page-sizes="[5, 8, 10, 12, 15]"
				@size-change="getData()"
				@current-change="getData"
			></el-pagination>
		</el-footer>
		<!--  -->
		<el-dialog
			:modal="false"
			width="700px"
			:visible="edit.isEdit"
			:show-close="false">
			<h2 slot="title" v-text="` - ${ edit.mode ? '' : '' }`" class="custom-dialog-title"></h2>
			<el-form label-width="130px" :model="edit.model" :rules="edit.validationRules" ref="editForm" status-icon>
				<el-form-item label=""  prop="stu_name">
					<el-input v-model.trim="edit.model.stu_name" placeholder=""></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-radio v-model="edit.model.stu_sex" :label="1"></el-radio>
					<el-radio v-model="edit.model.stu_sex" :label="0"></el-radio>
				</el-form-item>
				<el-form-item label="" prop="stu_phone">
					<el-input v-model.trim="edit.model.stu_phone" placeholder="" maxlength="11" show-word-limit></el-input>
				</el-form-item>
				<el-form-item label="" prop="stu_phone2">
					<el-input v-model.trim="edit.model.stu_phone2" placeholder="(/)">
						<template slot="append">028-88888888</template>
					</el-input>
				</el-form-item>
				<el-form-item label="" prop="stu_born">
					<el-date-picker
						v-model="edit.model.stu_born"
						placeholder=""
						:editable="false"
						value-format="yyyy-MM-dd"
						:picker-options="pickerOptions"></el-date-picker>
				</el-form-item>
				<el-form-item label="" prop="stu_qualification">
					<el-select v-model="edit.model.stu_qualification" placeholder="-  -">
						<el-option
							v-for="item in $store.getters['dictionary/getGroupByKey']('qualification')"
							:key="item.dic_id" :value="item.dic_id" :label="item.dic_name"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="/">
					<el-input v-model.trim="edit.model.stu_school" placeholder="/()"></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-input v-model.trim="edit.model.stu_major" placeholder="()"></el-input>
				</el-form-item>
				<el-form-item label="" prop="stu_address">
					<el-input v-model.trim="edit.model.stu_address" placeholder=""></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-input type="textarea" :rows="4" placeholder=".." v-model.trim="edit.model.stu_remark"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer">
				<el-button type="primary" size="medium" @click="save"></el-button>
				<el-button @click="edit.isEdit = false" size="medium"></el-button>
			</div>
		</el-dialog>
		<!--  -->
		<el-dialog
			:modal="false"
			width="700px"
			:visible="edit2.isEdit"
			:show-close="false">
			<h2 slot="title" class="custom-dialog-title"></h2>
			<el-form label-width="80px">
				<el-form-item label="">
					<el-select v-model="edit2.model.stu_cls_id">
						<el-option :value="null" label="-  -"></el-option>
						<el-option
							v-for="item in listClass"
							:key="item.cls_id"
							:value="item.cls_id"
							:label="item.cls_name"
							:disabled="item.cls_end !== null"></el-option>
					</el-select>
				</el-form-item>
			</el-form>
			<div slot="footer">
				<el-button type="primary" size="medium" @click="assignClass"></el-button>
				<el-button @click="edit2.isEdit = false" size="medium"></el-button>
			</div>
		</el-dialog>
		<!--  -->
		<el-dialog
			:modal="false"
			width="1100px"
			:visible="edit3.isEdit"
			:show-close="false">
			<h2 slot="title" class="custom-dialog-title"></h2>
			<el-form label-width="100px">
				<el-form-item label="">
					<div class="avatar-wrapper">
						<div class="avatar-wrapper-old" v-if="edit3.model.stu_avatar_old !== null">
							<el-image :src="edit3.model.stu_avatar_old"></el-image>
							<p></p>
						</div>
						<div class="seperator"  v-if="edit3.model.stu_avatar_old !== null"></div>
						<div class="avatar-wrapper-new">
							<el-upload
								ref="upload"
								class="avatar-uploader"
								list-type="picture-card"
								accept=".png,.jpg"
								action="/student/avatarupload"
								name="avatar"
								:headers="uploadHeaders"
								:before-upload="beforeUploadHandler"
								:on-success="successHandler"
								:on-remove="removeHandler">
								<i class="el-icon-plus avatar-uploader-icon"></i>
								<p slot="tip" style="color: red;">jpg/png2M, 5:7</p>
							</el-upload>
						</div>
					</div>
				</el-form-item>
			</el-form>
			<div slot="footer">
				<el-button type="primary" size="medium" :disabled="this.edit3.model.stu_avatar_new === null" @click="uploadAvatar"></el-button>
				<el-button @click="edit3.isEdit = false" size="medium"></el-button>
			</div>
		</el-dialog>
	</el-container>
</template>

<script type="text/ecmascript-6">
	import defaultAvatar from './default.jpg';
	import XLSX from 'xlsx';

        export default {
                name: 'Student',
                data() {
                        return {
                                defaultAvatar,
	                        pickerOptions: {
                                        disabledDate(time) {
                                                var year = new Date().getFullYear();
                                                var minYear = year - 18;
                                                return time.getTime() > new Date(minYear, 0, 1).getTime();
                                        }
	                        },
	                        uploadHeaders:  { Authorization: sessionStorage.getItem('token') },
                                list: [],                  // 
                                listClass: [],          // 
                                search: { stu_name: '', stu_cls_id: 0, stu_qualification: 0 },
                                pagination: { total: 0, pageSize: 5, currentPage: 1 },
	                        // edit/
	                        edit: {
                                        isEdit: false,
		                        mode: true,
		                        model: {        // 
                                                stu_id: null,
			                        stu_name: '',
			                        stu_avatar: null,
			                        stu_cls_id: null,
			                        stu_sex: 1,
			                        stu_phone: '',
			                        stu_phone2: '',
			                        stu_born: null,
			                        stu_qualification: null,
			                        stu_school: '',
			                        stu_major: '',
			                        stu_address: '',
			                        stu_remark: ''
		                        },
		                        validationRules: {
						stu_name: [
							{ required: true, message: '* ', trigger: 'blur' },
							{ min: 2, max: 20, message: '*  2 - 20', trigger: 'blur' }
						],
			                        stu_phone: [
				                        { required: true, message: '* ', trigger: 'blur' },
				                        { pattern: /^1[3584]\d{9}$/, message: '* ', trigger: 'blur' }
			                        ],
			                        stu_phone2: [
				                        { required: true, message: '* ', trigger: 'blur' },
				                        { pattern: /^((0\d{2,3}-\d{7,8})|(1[3584]\d{9}))$/, message: '* /', trigger: 'blur' }
			                        ],
						stu_born: [{ required: true, message: '* ', trigger: 'blur' }],
			                        stu_qualification: [{ required: true, message: '* ', trigger: 'change' }],
			                        stu_address: [
				                        { required: true, message: '* ', trigger: 'blur' },
				                        { min: 10, max: 100, message: ' 10 - 100', trigger: 'blur' }
			                        ]
		                        }
	                        },
	                        // edit2/
	                        edit2: {
                                        isEdit: false,
		                        model: {
                                                stu_id: null,   // nullnull
			                        stu_ids: [],     // 
			                        stu_cls_id: null
		                        }
	                        },
	                        // edit3
	                        edit3: {
                                        isEdit: false,
		                        model: { stu_id: null, stu_avatar_old: null, stu_avatar_new: null }
	                        },
	                        edit4: {
                                        isEdit: true,
		                        model: {  }
	                        }
                        };
                },
	        methods: {
                        getData(currentPage = 1) {
                                this.pagination.currentPage = currentPage;
                                // ajaxdatalist
                                this.$http({
                                        url: '/student/list',
                                        method: 'post',
                                        data: {
                                                ...this.search,
                                                begin: (this.pagination.currentPage - 1) * this.pagination.pageSize,
                                                pageSize: this.pagination.pageSize
                                        }
                                }).then(({ list, total }) => {
                                        this.list = list;
                                        this.pagination.total = total;
                                });
                        },
		        // /
                        beginAdd() {
                                this.edit.mode = true;
                                this.edit.isEdit = true;
                        },
                        beginUpdate(stu) {
                                this.edit.model.stu_id = stu.stu_id;
                                this.edit.model.stu_name = stu.stu_name;
                                this.edit.model.stu_avatar = stu.stu_avatar;
                                this.edit.model.stu_cls_id = stu.stu_cls_id;
                                this.edit.model.stu_sex = stu.stu_sex;
                                this.edit.model.stu_phone = stu.stu_phone;
                                this.edit.model.stu_phone2 = stu.stu_phone2;
                                this.edit.model.stu_born = stu.stu_born;
                                this.edit.model.stu_qualification = stu.stu_qualification;
                                this.edit.model.stu_school = stu.stu_school;
                                this.edit.model.stu_major = stu.stu_major;
                                this.edit.model.stu_address = stu.stu_address;
                                this.edit.model.stu_remark = stu.stu_remark;

                                this.edit.mode = false;
                                this.edit.isEdit = true;
                        },
                        _saveWithAdd() {
                                this.$http({ url: '/student/add', method: 'post', data: this.edit.model })
                                        .then(stu_id => {
                                                this.list.push(Object.assign({}, this.edit.model, { stu_id }));
                                                this.$message({
                                                        message: `"${ this.edit.model.stu_name }" `,
                                                        type: 'success'
                                                });
                                                this.edit.isEdit = false;
                                        });
                        },
                        _saveWithUpdate() {
                                this.$http({ url: '/student/update', method: 'post', data: this.edit.model })
                                        .then(() => {
                                                var i = this.list.findIndex(item => item.stu_id === this.edit.model.stu_id);
                                                this.list.splice(i, 1, Object.assign({}, this.edit.model));
                                                this.$message({ message: '',  type: 'success' });
                                                this.edit.isEdit = false;
                                        });
                        },
                        save() {
                                this.$refs.editForm.validate()
                                        .then(() => {            // 
                                                if(this.list.some(item => item.stu_id === this.edit.model.stu_id && item.stu_phone === this.edit.model.stu_phone)) return Promise.resolve();
                                                else {
                                                        // ajax
                                                        return this.$http({ url: '/student/valid_phone', method: 'post', data: { stu_phone: this.edit.model.stu_phone }})
                                                                .then(count => {
                                                                        if(count === 0) return Promise.resolve();
                                                                        else {
                                                                                this.$alert(`${ this.edit.model.stu_phone } !`, {
                                                                                        title: '',
                                                                                        type: 'warning',
                                                                                        showClose: false
                                                                                });
                                                                                return Promise.reject();
                                                                        }
                                                                });
                                                }
                                        })
                                        .then(() => {
                                                if(this.edit.mode) this._saveWithAdd();         // 
                                                else this._saveWithUpdate();                         // 
                                        })
                                        .catch(() => {});
                        },
		        // (/)
			selectionChangeHandler(rows) {
				this.edit2.model.stu_ids = rows.map(item => item.stu_id);
			},
			beginAssignClass(stu){
                                this.edit2.model.stu_id = stu.stu_id;
                                this.edit2.model.stu_cls_id = stu.stu_cls_id;
                                this.edit2.isEdit = true;
			},
		        beginAssignClassBatch() {
			        if(this.edit2.model.stu_ids.length === 0) {
					this.$alert('', { title: '', type: 'warning', showClose: false });
			        } else {
                                        this.edit2.model.stu_cls_id = null;
                                        this.edit2.isEdit = true;
			        }
		        },
		        assignClass() {
				this.$http({ url: '/student/assignclass', method: 'post', data: this.edit2.model })
					.then(() => {
						if(this.edit2.model.stu_id !== null) {          // 
							var i = this.list.findIndex(item => item.stu_id === this.edit2.model.stu_id);
							this.list[i].stu_cls_id = this.edit2.model.stu_cls_id;
						} else {                                                         // 
							this.list.forEach(item => {
							        if(this.edit2.model.stu_ids.indexOf(item.stu_id) !== -1)
							                item.stu_cls_id = this.edit2.model.stu_cls_id;
							});
						}
				                this.$message({ message: '',  type: 'success' });
				                this.edit2.isEdit = false;
					});
		        },
		        // 
		        beforeUploadHandler(file) {
                                const isJPG = file.type === 'image/jpeg';
                                const isPNG = file.type === 'image/png';
                                const isLt2M = file.size / 1024 / 1024 < 2;

                                if ((!isJPG) && (!isPNG)) {
                                        this.$message.error(' JPG PNG!');
                                }
                                if (!isLt2M) {
                                        this.$message.error(' 2MB!');
                                }
                                return (isJPG || isPNG) && isLt2M;
		        },
		        successHandler(response, file, fileList) {
			        if(response.status === 200) {
                                        this.edit3.model.stu_avatar_new = response.data;
                                        if(fileList.length > 1) fileList.splice(0, 1);
			        } else {
			                this.$message.error('  ');
			        }
		        },
		        removeHandler(file, fileList) { this.edit3.model.stu_avatar_new = null; },
		        beginUploadAvatar(stu) {
			        this.edit3.model.stu_id = stu.stu_id;
			        this.edit3.model.stu_avatar_old = stu.stu_avatar;
			        this.edit3.isEdit = true;
		        },
		        uploadAvatar() {
				this.$http({ url: '/student/avatarupdate', method: 'post', data: this.edit3.model })
					.then(stu_avatar => {
						var i = this.list.findIndex(item => item.stu_id === this.edit3.model.stu_id);
					        this.list[i].stu_avatar = stu_avatar;
                                                this.$message({ message: '',  type: 'success' });
					        this.edit3.isEdit = false;
					});
		        },
		        // 
		        beginExportStudent() {

		        },
		        exportStudent() {
                                var header = {
                                        header: ["stu_name"]
                                };
                                var workbook = XLSX.utils.book_new();
			        var worksheet = XLSX.utils.json_to_sheet(this.list, header);
			        XLSX.utils.book_append_sheet(workbook, worksheet, 'abc');
			        XLSX.writeFile(workbook, 'abc.xlsx');
		        }
	        },
	        created() {
                        // ajax
		        this.$http({ url: '/class/all', method: 'get' }).then(list => this.listClass = list);
		        // ajax
		        this.getData();
	        },
	        watch: {
                        'edit.isEdit'(newValue, oldValue) {
                                if(!newValue) {         // 
                                        this.edit.model.stu_id = null;
                                        this.edit.model.stu_name = '';
                                        this.edit.model.stu_avatar = null;
                                        this.edit.model.stu_cls_id = null;
                                        this.edit.model.stu_sex = 1;
                                        this.edit.model.stu_phone = '';
                                        this.edit.model.stu_phone2 = '';
                                        this.edit.model.stu_born = null;
                                        this.edit.model.stu_qualification = null;
                                        this.edit.model.stu_school = '';
                                        this.edit.model.stu_major = '';
                                        this.edit.model.stu_address = '';
                                        this.edit.model.stu_remark = '';
                                } else {                // 
                                        if(this.$refs.editForm)  // 
                                                this.$refs.editForm.clearValidate();
                                }
                        },
		        'edit2.isEdit'(newValue, oldValue) {
                                if(!newValue) {
                                        this.edit2.model.stu_id = null;
                                        this.edit2.model.stu_cls_id = null;
                                }
		        },
		        'edit3.isEdit'(newValue, oldValue) {
                                if(!newValue) {
                                        this.edit3.model.stu_id = null;
                                        this.edit3.model.stu_avatar_old = null;
                                        this.edit3.model.stu_avatar_new = null;
					this.$refs.upload.clearFiles();         // 
                                }
		        }
	        }
        };
</script>

<style scoped>
	.el-container { height: 100%; }
	.el-main { position: relative; }
	.el-main>.table-wrapper {
		box-sizing: border-box;
		position: absolute;
		width: 100%;
		height: 100%;
		left: 0; top: 0;
		padding: 10px;
		overflow: hidden;
	}

	h2.custom-dialog-title {
		background-color: #0094ff;
		text-indent: 2em;
		color: #fff;
		height: 40px;
		line-height: 40px;
		font-size: 18px;
	}

	/*  */
	.avatar-uploader /deep/ .el-upload {
		border: 1px dashed #d9d9d9;
		border-radius: 6px;
		cursor: pointer;
		position: relative;
		overflow: hidden;
		width: 200px;
		height: 280px;
	}
	.avatar-uploader /deep/ .el-upload:hover {
		border-color: #409EFF;
	}
	.avatar-uploader /deep/ .el-upload-list__item {
		width: 200px;
