<template>
	<div class="main-in-wrapper">
		<el-row :gutter="20" style="button: 20px; width: 100%">
			<!--card-->
			<transition-group name="edit">
				<el-col :span="6" v-for="item in list" :key="item.role_id">
					<!--card-->
					<el-card shadow="hover"
					         @mouseover.native="hoverId = item.role_id"
					         @mouseout.native="hoverId = 0">
						<span v-text="item.role_name"></span>
						<div class="mask" v-show="item.role_id === hoverId">
							<i class="el-icon-edit" @click="beginUpdate(item)"></i>
							<i class="el-icon-delete" @click="removeHandler(item)"></i>
						</div>
					</el-card>
				</el-col>
			</transition-group>
			<el-col :span="6">
				<el-card @click.native="beginAdd">
					<i class="el-icon-plus" ></i>
				</el-card>
			</el-col>
		</el-row>
		<!---->
		<el-dialog  :visible="edit.isEdit" :show-close="false" width="600px" :modal="false">
			<h3 slot="title" v-text="` - ${ edit.mode ? '' : ''}`" class="custom-dialog-title"></h3>
			<el-form label-width="110px" :model="edit.model" :rules="edit.validationRules" ref="editForm" status-icon>
				<el-form-item label="" prop="role_name" >
					<el-input v-model.trim="edit.model.role_name" placeholder="--"></el-input>
				</el-form-item>
			</el-form>
			<span slot="footer">
				<el-button size="medium" type="primary" @click="save"></el-button>
				<el-button size="medium" @click="edit.isEdit = false"></el-button>
			</span>
		</el-dialog>
	</div>
</template>

<script type="text/ecmascript-6">
	// 
	import { createNamespacedHelpers } from 'vuex';
	var { mapState, mapActions } = createNamespacedHelpers('role');

        export default {
                name: 'Role',
                data: function(){
                        return {
                                hoverId: 0,
                                edit: {
                                        isEdit: false,       // 
                                        mode: true,       // 
                                        model: { role_id: 0, role_name: ''},
                                        validationRules: {
                                                // 
                                                role_name: [
                                                        {
                                                                // func_namevaluecallback
                                                                validator: (rule, value, callback) => {
                                                                        if(value === '')
                                                                                callback(new Error(''));
                                                                        else if(value.length < 3 || value.length > 20)
                                                                                callback(new Error('2 - 20'));
                                                                        else if(
                                                                                this.$store.state.role.list.some(item =>
                                                                                        item.role_name === value &&
                                                                                        item.role_id !== this.edit.model.role_id)
                                                                        )
                                                                                callback(new Error(''));
                                                                        else
                                                                                callback();
                                                                },
                                                                trigger: 'blur'        // trigger
                                                        }
                                                ]
                                        }
                                }
                        }
                },
	        computed: mapState(['list']),
                methods: {
	                ...mapActions(['init', 'remove', 'add', 'update']),
                        removeHandler: function(data){
                                this.$confirm(`"${ data.role_name }" ? `,  '', {
                                        showClose: false,
                                        type: 'warning'
                                })
                                        // funcactionsremove
//                                        .then(() => this.$store.dispatch('role/remove', data.role_id))
	                                .then(() => this.remove(data.role_id))
	                                .then(() => this.$message({ message: `"${ data.role_name }" `, type: 'success' }))
	                                .catch(() => {});
                        },
                        beginAdd: function(data){
                                this.edit.mode = true;                                    // 
                                this.edit.model.role_id = 0;
                                this.edit.model.role_name = '';
                                this.edit.isEdit = true;          // 
                        },
                        beginUpdate: function(data){
                                this.edit.mode = false;
                                this.edit.model.role_id = data.role_id;
                                this.edit.model.role_name = data.role_name;
                                this.edit.isEdit = true;        // 
                        },
                        save: function(){
                                // ajaxvalidate()
                                // isValid
                                this.$refs.editForm.validate(isValid => {
                                        if(isValid) {
                                                if(this.edit.mode) {         // 
//                                                        this.$store.dispatch('role/add', this.edit.model)
	                                                this.add(this.edit.model)
                                                                .then(() =>{
                                                                        this.edit.isEdit = false;
                                                                        this.$message({
                                                                                message: `: "${ this.edit.model.role_name }" !`,
                                                                                type: 'success'
                                                                        })
                                                                });
                                                } else {                            // 
//                                                        this.$store.dispatch('role/update', this.edit.model)
	                                                this.update(this.edit.model)
                                                                .then(() =>{
                                                                        this.edit.isEdit = false;
                                                                        this.$message({
                                                                                message: `: "${ this.edit.model.role_name }" !`,
                                                                                type: 'success'
                                                                        })
                                                                });
                                                }
                                        }
                                });

                        }
                },
                created: function(){
//                        this.$store.dispatch('role/init');
			this.init();
                },
                watch: {        // 
                        'edit.isEdit': function(newValue, oldValue){    // 
                                if(!newValue) {
                                        this.$refs.editForm.resetFields();
                                }
                        }
                }
        };
</script>

<style scoped>
	.main-in-wrapper {
		height: 100%;
	}
	/**/
	.mask {
		position: absolute;
		top: 0;
		left:0;
		width: 100%;
		height: 100%;
		background-color: rgba(0,0,0,0.6);
		display: flex;
		justify-content: center;
		align-items: center;
	}
	.mask i {
		font-size: 30px;
		color: #fff;
		margin: 0 10px;
		cursor: pointer;
	}
	.el-card {
		margin: 15px 0;
		height: 110px;
		position: relative;
		display: flex;
		justify-content: center;
		align-items: center;
		background-color: rgb(203,225,239);
	}
	/**/
	.edit-enter, .edit-leave-to { opacity: 0; }
	.edit-enter-active, .edit-leave-active { transition: all 1s ease-in 0.5s; }
</style>