<template>
	<el-container>
		<el-header height="40px">
			<el-form :inline="true">
				<el-form-item label="">
					<el-input
						clearable
						placeholder=""
						prefix-icon="el-icon-search"
						v-model.trim="search.cls_name"
						@change="getData()"></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-select v-model="search.cls_dic_id_major" @change="getData()">
						<i slot="prefix" class="el-icon-search"></i>
						<el-option :value="0" label="-  -"></el-option>
						<el-option
							v-for="item in $store.getters['dictionary/getGroupByKey']('class_major')"
							:key="item.dic_id"
							:value="item.dic_id"
							:label="item.dic_name"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="">
					<el-select v-model="search.cls_status" @change="getData()">
						<el-option :value="0" label="-  -"></el-option>
						<el-option :value="1" label=""></el-option>
						<el-option :value="2" label=""></el-option>
						<el-option :value="3" label=""></el-option>
					</el-select>
				</el-form-item>
				<el-form-item style="float: right">
					<el-button type="success" size="medium" icon="el-icon-plus" @click="beginAdd"></el-button>
				</el-form-item>
			</el-form>
		</el-header>
		<el-main>
			<div class="table-wrapper">
				<el-table border stripe :highlight-current-row="true" :data="list" height="100%">
					<el-table-column type="index" label="#" align="center" fixed></el-table-column>
					<el-table-column label="" prop="cls_name" width="130" fixed></el-table-column>
					<el-table-column label="" width="120">
						<template slot-scope="{ row }">
							<span v-text="$store.getters['dictionary/getNameById'](row.cls_dic_id_major)"></span>
						</template>
					</el-table-column>
					<el-table-column label="" width="100" align="center">
						<template slot-scope="{ row }">
							<span v-text="staffListTeacher.find(item => item.stf_id === row.cls_stf_id_teacher).stf_name"></span>
						</template>
					</el-table-column>
					<el-table-column label="" width="100" align="center">
						<template slot-scope="{ row }">
							<span v-text="staffListAdmin.find(item => item.stf_id === row.cls_stf_id_admin).stf_name"></span>
						</template>
					</el-table-column>
					<el-table-column label="" width="100" align="center">
						<template slot-scope="{ row }">
							<span v-text="staffListJob.find(item => item.stf_id === row.cls_stf_id_job).stf_name"></span>
						</template>
					</el-table-column>
					<el-table-column label="" width="100">
						<template slot-scope="{ row }">
							<span v-if="row.cls_clsr_id === null"></span>
							<span v-else v-text="$store.getters['classroom/getNameById'](row.cls_clsr_id)"></span>
						</template>
					</el-table-column>
					<el-table-column label="" prop="cls_begin" width="120" align="center"></el-table-column>
					<el-table-column label="" prop="cls_end" width="120" align="center"></el-table-column>
					<el-table-column label="" width="100" align="center">
						<template slot-scope="{ row }">
							<span v-if="row.cls_begin === null" class="do"></span>
							<span v-else-if="row.cls_end === null" class="doing"></span>
							<span v-else class="done"></span>
						</template>
					</el-table-column>
					<el-table-column label="" show-overflow-tooltip>
						<template slot-scope="{ row }">
							<span :class="{empty : !row.cls_remark}" v-text="row.cls_remark || '...' "></span>
						</template>
					</el-table-column>
					<el-table-column label="" width="200" fixed="right">
						<template slot-scope="{ row }">
							<el-button type="primary" size="mini" icon="el-icon-edit" @click="beginUpdate(row)"></el-button>
							<el-button type="success" size="mini" v-if="row.cls_begin === null" @click="beginClassBegin(row)"></el-button>
							<el-button type="danger" size="mini" v-else-if="row.cls_end === null" @click="classEnd(row)"></el-button>
						</template>
					</el-table-column>
				</el-table>
			</div>
		</el-main>
		<el-footer height="32px">
			<el-pagination
				background
				:total="pagination.total"
				:page-size.sync="pagination.pageSize"
				:current-page.sync="pagination.currentPage"
				layout="total, prev, pager, next, jumper, ->, sizes"
				:page-sizes="[5, 8, 10, 12, 15]"
				@current-change="getData"
				@size-change="getData()"
			></el-pagination>
		</el-footer>
		<!--  -->
		<el-dialog
			:modal="false"
			width="700px"
			:visible="edit.isEdit"
			:show-close="false">
			<h2 slot="title" v-text="` - ${ edit.mode ? '' : '' }`" class="custom-dialog-title"></h2>
			<el-form label-width="100px" :model="edit.model" :rules="edit.validationRules" ref="editForm" status-icon>
				<el-form-item label=""  prop="cls_name">
					<el-input v-model.trim="edit.model.cls_name" placeholder=""></el-input>
				</el-form-item>
				<el-form-item label="" prop="cls_dic_id_major">
					<el-select v-model="edit.model.cls_dic_id_major">
						<el-option
							v-for="item in $store.getters['dictionary/getGroupByKey']('class_major')"
							:key="item.dic_id"
							:value="item.dic_id"
							:label="item.dic_name"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="" v-show="edit.model.cls_begin !== null && edit.model.cls_end === null">
					<el-select v-model="edit.model.cls_clsr_id">
						<el-option
							v-for="item in $store.state.classroom.list"
							:key="item.clsr_id"
							:value="item.clsr_id"
							:label="item.clsr_name"
							:disabled="
								(!!item.clsr_occupy) &&
								!list.some(item2 => item2.cls_id === edit.model.cls_id && item2.cls_clsr_id === item.clsr_id)
							"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="" prop="cls_stf_id_teacher">
					<el-select v-model="edit.model.cls_stf_id_teacher">
						<el-option v-for="item in staffListTeacher" :key="item.stf_id" :value="item.stf_id" :label="item.stf_name"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="" prop="cls_stf_id_admin">
					<el-select v-model="edit.model.cls_stf_id_admin">
						<el-option v-for="item in staffListAdmin" :key="item.stf_id" :value="item.stf_id" :label="item.stf_name"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="" prop="cls_stf_id_job">
					<el-select v-model="edit.model.cls_stf_id_job">
						<el-option v-for="item in staffListJob" :key="item.stf_id" :value="item.stf_id" :label="item.stf_name"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="">
					<el-input type="textarea" :rows="4" placeholder=".." v-model.trim="edit.model.cls_remark"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer">
				<el-button type="primary" size="medium" @click="save"></el-button>
				<el-button @click="edit.isEdit = false" size="medium"></el-button>
			</div>
		</el-dialog>
		<!--  -->
		<el-dialog
			:modal="false"
			width="700px"
			:visible="edit2.isEdit"
			:show-close="false">
			<h2 slot="title" class="custom-dialog-title"></h2>
			<el-form label-width="100px" :model="edit2.model" :rules="edit2.validationRules" ref="editForm2" status-icon>
				<el-form-item label="" prop="cls_clsr_id">
					<el-select v-model="edit2.model.cls_clsr_id">
						<el-option
							v-for="item in $store.state.classroom.list"
							:key="item.clsr_id"
							:value="item.clsr_id"
							:label="item.clsr_name"
							:disabled="!!item.clsr_occupy"></el-option>
					</el-select>
				</el-form-item>
			</el-form>
			<div slot="footer">
				<el-button type="primary" size="medium" @click="classBegin"></el-button>
				<el-button @click="edit2.isEdit = false" size="medium"></el-button>
			</div>
		</el-dialog>
	</el-container>
</template>

<script type="text/ecmascript-6">
        export default {
                name: 'Class',
		data() {
		        return {
		                list: [],
			        staffListTeacher: [],
			        staffListAdmin: [],
			        staffListJob: [],
		                // 
				search: { cls_name: '', cls_dic_id_major: 0, cls_status: 0 },
			        pagination: { total: 0, currentPage: 1, pageSize: 5 },
			        edit: {   // 
		                        isEdit: false,
				        mode: true,
				        model: { cls_id: 0, cls_name: '', cls_dic_id_major: null, cls_clsr_id: null, cls_stf_id_teacher: null, cls_stf_id_admin: null, cls_stf_id_job: null, cls_begin: null, cls_end: null, cls_remark: null },
				        validationRules: {
		                                cls_name: [{
		                                        validator: (rule, value, callback) => {
		                                                if(value === '') callback(new Error('* '));
		                                                else if(value.length < 2 || value.length > 20) callback(new Error('*  2 - 20'));
		                                                else if(this.list.some(item => item.cls_id === this.edit.model.cls_id && item.cls_name === value)) callback();
		                                                else {
		                                                        // ajax
			                                                this.$http({
										url: '/class/valid_name',
				                                                method: 'post',
				                                                data: { cls_name: value }
			                                                }).then(count => {
										if(count === 0) callback();
										else callback(new Error('* '));
			                                                });
		                                                }
		                                        },
			                                trigger: 'blur'
		                                }],
					        cls_dic_id_major: [{ required: true, message: '* ', trigger: 'change' }],
					        cls_stf_id_teacher: [{ required: true, message: '* ', trigger: 'change' }],
					        cls_stf_id_admin: [{ required: true, message: '* ', trigger: 'change' }],
					        cls_stf_id_job: [{ required: true, message: '* ', trigger: 'change' }]
				        }
			        },
			        edit2: {  // 
		                        isEdit: false,
				        model: { cls_id: 0, cls_clsr_id: null },
				        validationRules: {
		                                cls_clsr_id: [
			                                { required: true, message: '* ', trigger: 'change' }
		                                ]
				        }
			        }
		        };
		},
	        methods: {
                        // 
                        getData(currentPage = 1) {
                                this.pagination.currentPage = currentPage;
                                // ajaxdatalist
                                this.$http({
                                        url: '/class/list',
                                        method: 'post',
                                        data: {
                                                ...this.search,
                                                begin: (this.pagination.currentPage - 1) * this.pagination.pageSize,
                                                pageSize: this.pagination.pageSize
                                        }
                                }).then(({ list, total }) => {
                                        this.list = list;
                                        this.pagination.total = total;
                                });
                        },
		        beginAdd() {
                                this.edit.mode = true;
                                this.edit.isEdit = true;
		        },
		        beginUpdate(cls) {
                                this.edit.model.cls_id = cls.cls_id;
                                this.edit.model.cls_name = cls.cls_name;
                                this.edit.model.cls_dic_id_major = cls.cls_dic_id_major;
                                this.edit.model.cls_clsr_id = cls.cls_clsr_id;
                                this.edit.model.cls_stf_id_teacher = cls.cls_stf_id_teacher;
                                this.edit.model.cls_stf_id_admin = cls.cls_stf_id_admin;
                                this.edit.model.cls_stf_id_job = cls.cls_stf_id_job;
                                this.edit.model.cls_begin = cls.cls_begin;
                                this.edit.model.cls_end = cls.cls_end;
                                this.edit.model.cls_remark = cls.cls_remark;

				this.edit.mode = false;
                                this.edit.isEdit = true;
		        },
                        // /
		        _saveWithAdd() {
                                this.$http({ url: '/class/add', method: 'post', data: this.edit.model })
                                        .then(cls_id => {
                                                this.list.push(Object.assign({}, this.edit.model, { cls_id }));
                                                this.$message({
                                                        message: `"${ this.edit.model.cls_name }" `,
                                                        type: 'success'
                                                });
                                                this.edit.isEdit = false;
                                        });
                        },
                        _saveWithUpdate() {
                                this.$http({ url: '/class/update', method: 'post', data: this.edit.model })
                                        .then(() => {
                                                var i = this.list.findIndex(item => item.cls_id === this.edit.model.cls_id);
                                                // 
						if(this.list[i].cls_clsr_id !== this.edit.model.cls_clsr_id) {
							this.$store.commit('classroom/toggleStatus', this.list[i].cls_clsr_id);
                                                        this.$store.commit('classroom/toggleStatus', this.edit.model.cls_clsr_id);
						}
                                                this.list.splice(i, 1, Object.assign({}, this.edit.model));
                                                this.$message({ message: '',  type: 'success' });
                                                this.edit.isEdit = false;
                                        });
                        },
                        save() {
                                this.$refs.editForm.validate(isValid => {       // 
                                        if(isValid) {
                                                // 
                                                if(this.edit.mode) this._saveWithAdd();
                                                // 
                                                else this._saveWithUpdate();
                                        }
                                });
                        },
		        // 
		        beginClassBegin(cls) {
                                this.$confirm(
                                        `"${ cls.cls_name }"   `,
                                        '',
                                        { showClose: false,  type: 'warning' }
                                )
	                                .then(() => {
                                                this.edit2.model.cls_id = cls.cls_id;
                                                this.edit2.isEdit = true;
	                                })
	                                .catch(() => {});
		        },
		        classBegin() {
                                this.$refs.editForm2.validate(isValid => {
                                        if(isValid) {
                                                this.$http({
							url: '/class/begin',
	                                                method: 'post',
	                                                data: this.edit2.model
                                                })
	                                                .then(cls_begin => {
	                                                        var i = this.list.findIndex(item => item.cls_id === this.edit2.model.cls_id);
	                                                        this.list.splice(i, 1, Object.assign({}, this.list[i], { cls_begin, cls_clsr_id: this.edit2.model.cls_clsr_id }));
								this.$store.commit('classroom/toggleStatus', this.edit2.model.cls_clsr_id);
                                                                this.$message({ message: '', type: 'success' });
								this.edit2.isEdit = false;
	                                                });
                                        }
                                });
		        },
		        // 
		        classEnd(cls) {
                                this.$confirm(
                                        `"${ cls.cls_name }"   `,
                                        '',
                                        { showClose: false,  type: 'warning' }
                                )
	                                .then(() => this.$http({ url: '/class/end', method: 'post', data: { cls_id: cls.cls_id }}))
	                                .then(cls_end => {
                                                var i = this.list.findIndex(item => item.cls_id === cls.cls_id);
                                                this.list.splice(i, 1, Object.assign({}, this.list[i], { cls_end }));
                                                this.$store.commit('classroom/toggleStatus', this.list[i].cls_clsr_id);
                                                this.$message({ message: '', type: 'success' });
	                                })
	                                .catch(() => {});
		        }
	        },
	        created() {
                        // classroom
                        this.$store.dispatch('classroom/init');
			// 
			this.$http({ url: '/staff/listbycategory/4', method: 'get' }).then(list => this.staffListTeacher = list);
		        // 
                        this.$http({ url: '/staff/listbycategory/5', method: 'get' }).then(list => this.staffListAdmin = list);
		        // 
                        this.$http({ url: '/staff/listbycategory/6', method: 'get' }).then(list => this.staffListJob = list);
                        // 
                        this.getData();
	        },
	        watch: {
                        'edit.isEdit'(newValue, oldValue) {
                                if(!newValue) { // 
					this.edit.model.cls_id = 0;
					this.edit.model.cls_name = '';
					this.edit.model.cls_dic_id_major = null;
					this.edit.model.cls_clsr_id = null;
					this.edit.model.cls_stf_id_teacher = null;
					this.edit.model.cls_stf_id_admin = null;
					this.edit.model.cls_stf_id_job = null;
					this.edit.model.cls_begin = null;
					this.edit.model.cls_end = null;
					this.edit.model.cls_remark = null;
                                } else {               // 
					if(this.$refs.editForm)  // 
					         this.$refs.editForm.clearValidate();
                                }
                        },
		        'edit2.isEdit'(newValue, oldValue) {
                                if(!newValue) {
	                                this.edit2.model.cls_id = 0;
	                                this.edit2.model.cls_clsr_id = null;
                                } else {
                                        if(this.$refs.editForm2)
                                                this.$refs.editForm2.clearValidate();
                                }
		        }
	        }
        };
</script>

<style scoped>
	.el-container { height: 100%; }
	.el-main { position: relative; }
	.el-main>.table-wrapper {
		box-sizing: border-box;
		position: absolute;
		width: 100%;
		height: 100%;
		left: 0; top: 0;
		padding: 10px;
		overflow: hidden;
	}

	span.empty { color: #ccc; }
	span.do { color: #E6A23C; }
	span.doing { color: #67C23A; }
	span.done { color: #ccc; }

	h2.custom-dialog-title {
		background-color: #0094ff;
		text-indent: 2em;
		color: #fff;
		height: 40px;
		line-height: 40px;
		font-size: 18px;
	}
</style>