<template>
	<div class="page-wrapper">
		<h1>ST - </h1>
		<div class="login-wrapper">
			<div class="form-wrapper">
				<h2> - </h2>
				<el-form ref="loginForm" :model="model" :rules="validateRules">
					<el-form-item prop="user_name">
						<el-input v-model.trim="model.user_name">
							<div slot="prepend" class="custom-input-prepend">
								<i class="el-icon-user-solid"></i> 
							</div>
						</el-input>
					</el-form-item>
					<el-form-item prop="user_pwd">
						<el-input type="password" v-model.trim="model.user_pwd">
							<div slot="prepend" class="custom-input-prepend">
								<i class="el-icon-lock"></i> 
							</div>
						</el-input>
					</el-form-item>
					<el-form-item>
						<el-button type="primary" @click="login"></el-button>
					</el-form-item>
				</el-form>
			</div>
		</div>

	</div>
</template>

<script type="text/ecmascript-6">
        export default {
                name: 'Login',
	        data() {
                        return {
                                model: { user_name: '', user_pwd: '' },
                                validateRules: {
					user_name: [
						{ required: true, message: '', trigger: 'blur' },
						{ min: 2, max: 20, message: ' 2 - 20', trigger: 'blur' }
					],
	                                user_pwd: [
                                                { required: true, message: '', trigger: 'blur' },
                                                { min: 2, max: 20, message: ' 2 - 20', trigger: 'blur' }
	                                ]
                                }
                        };
	        },
	        methods: {
                        login() {
                                this.$refs.loginForm.validate(valid => {
                                        if(valid) {
                                                this.$http({ url: '/user/login', method: 'post', data: this.model })
	                                                .then(token => {
	                                                        // tokentoken
								sessionStorage.setItem('token', token);
								// prev_user
                                                                sessionStorage.setItem('prev_user', sessionStorage.getItem('user') || '');
                                                                // user
								sessionStorage.setItem('user', this.model.user_name);
								// 
                                                                this.$message({ message: ``, type: 'success' });
                                                                // 
								this.$router.replace('/home');
                                                        });
                                        }
                                });
                        }
	        },
	        created() { sessionStorage.removeItem('isExist401'); }
        };
</script>

<style scoped>
	@keyframes fall {                                                        /*  */
		0% { opacity: 0;  transform: scale(0) translateY(20%) rotateY(0deg); }
		40% { opacity: 1;  transform: scale(1) translateY(20%) rotateY(0deg);  }
		60% { opacity: 1;  transform: scale(1) translateY(20%) rotateY(45deg); right: 50%; }
		100% { opacity: 1;  transform: scale(1) translateY(20%) rotateY(45deg); right: 40%;  }
	}
	.page-wrapper { height: 100%; position: relative; perspective: 400px; /* 3D */ }
	.page-wrapper>h1 {
		padding: 10px 50px;
		position: absolute; right: 50%; bottom: 50%;
		transform-origin: left center;                            /*  */
		font-size: 150px;
		color: #0094ff;
		border-bottom: 1px solid #0094ff;
		text-shadow: -10px 10px 0 #000;
		opacity: 0;
		transform: translateY(20%) scale(0);
		 /* css3 */
		animation-name: fall;                                           /*  */
		animation-duration: 2s;                                      /*  */
		animation-timing-function: ease-in;                 /*  */
		animation-fill-mode: forwards;                          /*  */
		animation-delay: 2s;                                            /*  */
	}
	.page-wrapper>h1:before {
		content: 'ST - ';
		padding: 10px 50px;
		font-size: 150px;
		color: #0094ff;
		text-shadow: -10px 10px 0 #000;
		position: absolute; left: 0; top: 100%;
		transform: scaleY(-0.8);
	}
	.page-wrapper>h1:after {
		content: '';
		position: absolute; left: 0; top: 100%;
		width: 100%; height: 100%;
		/*  -  */
		background-image: linear-gradient(to bottom, transparent , #fff);
	}





	@keyframes slide {
		0% { margin-left: 110%; }
		100% { margin-left: 0%; }
	}
	.login-wrapper {
		position: absolute; top: 50%; left: 50%;
		transform: translateY(-50%);
		width: 500px;
		height: 350px;
		overflow: hidden;
		padding: 10px;
	}
	.form-wrapper {
		width: 100%;
		height: 100%;
		background-color: #ccc;
		box-shadow: 0 0 10px 1px #ccc;
		border-radius: 2px;
		margin-left: 110%;
		/* css3 */
		animation-name: slide;                                           /*  */
		animation-duration: 0.6s;                                      /*  */
		animation-timing-function: linear;                 /*  */
		animation-fill-mode: forwards;                          /*  */
		animation-delay: 1s;                                            /*  */
	}
	.form-wrapper>h2 {
		text-align: center;
		padding: 40px 0;
		color: #0094ff;
		font-size: 26px;
		text-shadow: 0 1px 0px #fff;
	}
	.el-form {padding: 0 30px;}
	.el-form-item { margin-bottom: 30px; text-align: center; }
	.custom-input-prepend { width: 60px; }
</style>