// http,axios
import axios from 'axios';
import { Loading, Message, MessageBox } from 'element-ui';
import router from '../router';

var alwaysPendingPromise = new Promise(() => {});

function http(options) {
        var loading = Loading.service({                                         // loading
                lock: true,
                text: 'Loading',
                spinner: 'el-icon-loading',
                background: 'rgba(0, 0, 0, 0.7)'
        });
        // ajaxtoken
        options.headers = { Authorization: sessionStorage.getItem('token') };
        return axios(options)
        // loading0.5s
                .then(response => {
                        return new Promise(resolve => {
                                setTimeout(() => {
                                        loading.close();                                 // loading
                                        resolve(response);
                                }, 500);
                        })
                })
                .then(response => {		// 
                        if(response.status === 200) return Promise.resolve(response.data);
                        else return Promise.reject(new Error(response.statusText));
                })
                .then(result => {		// ()
                        switch(result.status) {
                                case 200:
                                        return Promise.resolve(result.data);
                                case 401:       // 
                                        sessionStorage.removeItem('token');             // home
                                        // isExist401sessionStorage
                                        if(!sessionStorage.getItem('isExist401')) {
                                                sessionStorage.setItem('isExist401', true);
                                                MessageBox({
                                                        title: '',
                                                        message: '!',
                                                        type: 'warning',
                                                        showClose: false,                                    // 
                                                        closeOnClickModal: false,                      // 
                                                        closeOnPressEscape: false                      // ESC
                                                })
                                                        .then(() => router.replace('/login'));
                                        }
                                        return alwaysPendingPromise;
                                case 404:       // 
                                        MessageBox({ title: '', message: '!', type: 'warning' })
                                                .then(() => { /*  */ });
                                        return alwaysPendingPromise;
                                case 199:       // 
                                case 500:       // 
                                        return Promise.reject(new Error(result.message));
                        }
                })
                // /  catchnthen
                .catch(function(error) {
                        Message({ message: error.message, type: 'error' });
                        loading.close();                                 // loading
                        return alwaysPendingPromise;
                });
}
// http
export default http;