<template>
	<div class="custom-tree-container">
		<!--  -->
		<el-tree
			class="custom-tree"
			:data="treeData"
			:default-expand-all="true"
			:expand-on-click-node="false"
			:props="{ label: 'func_name', children: 'children' }">
			<!-- slot-scope -->
			<div class="custom-tree-node" slot-scope="{ data }">
				<span v-text="data.func_name"></span>
				<span class="right">
					<i class="el-icon-plus" v-if="data.func_key === ''" @click="beginAdd(data)"></i>
					<i
						class="el-icon-edit"
						v-if="data.func_id !== 0 && data.func_id !== 44 && data.func_id !== 45"
						@click="beginUpdate(data)"></i>
					<i
						class="el-icon-delete"
						v-if="data.func_id !== 0 && data.func_id !== 44 && data.func_id !== 45 && !data.children"
						@click="removeHandler(data)"></i>
				</span>
			</div>
		</el-tree>

		<!--  -->
		<el-dialog
			:modal="false"
			width="700px"
			:visible="edit.isEdit"
			:show-close="false">
			<h2 slot="title" v-text="` - ${ edit.mode ? '' : '' }`" class="custom-dialog-title"></h2>
			<el-form label-width="150px" :model="edit.model" :rules="edit.validationRules" ref="editForm">
				<el-form-item label="" v-show="edit.mode">
					<span v-text="edit.fName"></span>
				</el-form-item>
				<el-form-item label="" v-show="edit.mode">
					<el-radio v-model="edit.isLeaf" :label="false" :disabled="edit.model.func_fid !== 0"></el-radio>
					<el-radio v-model="edit.isLeaf" :label="true" :disabled="edit.model.func_fid !== 0"></el-radio>
				</el-form-item>
				<el-form-item label=""  prop="func_name">
					<el-input v-model.trim="edit.model.func_name" placeholder=""></el-input>
				</el-form-item>
				<el-form-item label="" v-show="edit.isLeaf" prop="func_key">
					<el-select placeholder="-  -" v-model="edit.model.func_key">
						<el-option v-for="(value, p) in Views" :key="p" :value="p" :label="p" :disabled="isFuncKeyUsed(p)">
							<el-popover trigger="hover" :content="value.remark" placement="right">
								<span v-text="p" slot="reference" class="custom-select-option"></span>
							</el-popover>
						</el-option>
					</el-select>
				</el-form-item>
			</el-form>
			<div slot="footer">
				<el-button type="primary" @click="save"></el-button>
				<el-button @click="edit.isEdit = false"></el-button>
			</div>
		</el-dialog>
	</div>
</template>

<script type="text/ecmascript-6">
	import Views from '../index.js';

        export default {
                name: 'Func',
	        data: function() {
                        return {
                                Views,
                                edit: {
                                        isEdit: false,                // 
	                                mode: true,                // 
	                                isLeaf: false,               // 
	                                fName: '',                   // 
	                                model: { func_id: 0, func_name: '', func_key: '', func_fid: -1 },
	                                validationRules: {
                                                // 
		                                func_name: [
			                                {
                                                                validator: (rule, value, callback) => {
                                                                        if(value === '')
                                                                                callback(new Error(''));
                                                                        else if(value.length < 2 || value.length > 20)
                                                                                callback(new Error(' 2 - 20'));
                                                                        else if(this._isFuncNameExist(value))
                                                                                callback(new Error(''));
                                                                        else
                                                                                callback();
                                                                },
				                                trigger: 'blur'
			                                }
		                                ],
		                                func_key: [
			                                {
			                                        validator: (rule, value, callback) => {
			                                                if(this.edit.isLeaf && value === '')
			                                                        callback(new Error(''));
			                                                else
			                                                        callback();
			                                        },
				                                trigger: 'change'
			                                }
		                                ]
	                                }
                                }
                        };
	        },
	        computed: {
                        isFuncKeyUsed: function() {
                                return func_key => this.$store.state.func.list.some(item => item.func_key === func_key);
                        },
		        treeData: function() {
                                return [{
                                        func_id: 0,
	                                func_name: 'ROOT',
	                                func_key: '',
	                                func_fid: -1,
	                                children: this.$store.getters['func/listWithTree']
                                }];
		        }
	        },
	        methods: {
                        // 
                        _isFuncNameExist: function(func_name) {
                                if(this.edit.mode)
					return this.$store.state.func.list.some(item =>
						item.func_name === func_name &&
						item.func_fid === this.edit.model.func_fid);
                                else
                                        return this.$store.state.func.list.some(item =>
	                                        item.func_name === func_name &&
	                                        item.func_fid === this.edit.model.func_fid &&
	                                        item.func_id !== this.edit.model.func_id);
                        },
                        removeHandler: function(data) {
				this.$confirm(
				        `"${ data.func_name }" `,
					'',
					{ showClose: false,  type: 'warning' }
				)
                                        // funcactionsremove
					.then(() => this.$store.dispatch('func/remove', data.func_id))
                                        // 
					.then(() => this.$message({ message: `"${ data.func_name }" `, type: 'success' }))
					.catch(() => {});
                        },
		        beginAdd: function(data) {
				this.edit.mode = true;
				this.edit.isLeaf = data.func_id !== 0;
				this.edit.fName = data.func_name;

				this.edit.model.func_id = 0;
				this.edit.model.func_name = '';
				this.edit.model.func_key = '';
				this.edit.model.func_fid = data.func_id;

                                this.edit.isEdit = true;
		        },
		        beginUpdate: function(data) {
                                // 
			        // 
			        // 
				this.edit.mode = false;
				this.edit.isLeaf = data.func_key !== '';
				this.edit.fName = '';

				this.edit.model.func_id = data.func_id;
				this.edit.model.func_name = data.func_name;
				this.edit.model.func_key = data.func_key;
				this.edit.model.func_fid = data.func_fid;

				this.edit.isEdit = true;
		        },
		        save: function() {
                                this.$refs.editForm.validate(isValid => {       // 
					if(isValid) {
                                                if(this.edit.mode) {            // 
                                                        this.$store.dispatch('func/add', this.edit.model)
                                                                .then(() => {
                                                                        this.edit.isEdit = false;
                                                                        this.$message({
                                                                                message: `"${ this.edit.model.func_name }" `,
                                                                                type: 'success'
                                                                        });
                                                                });
                                                }
                                                else {                                // 
                                                        this.$store.dispatch('func/update', this.edit.model)
                                                                .then(() => {
                                                                        this.edit.isEdit = false;
                                                                        this.$message({
                                                                                message: `"${ this.edit.model.func_name }" `,
                                                                                type: 'success'
                                                                        });
                                                                });
                                                }
					}
                                });
		        }
	        },
                created: function() {
                        this.$store.dispatch('func/init');
                },
	        watch: {
                        'edit.isEdit': function(newValue, oldValue) {
				if(!newValue) {
				        this.$refs.editForm.resetFields();      // 
				}
                        },
		        'edit.isLeaf': function(newValue, oldValue) {
                                if(!newValue) this.edit.model.func_key = '';
		        }
	        }
        };
</script>

<style scoped>
	.el-tree { width: 400px; }
	.custom-tree-node {
		flex-grow: 1;
		display: flex;
		justify-content: space-between;
	}
	.custom-tree-node>span.right>* { margin-left: 10px; }

	h2.custom-dialog-title {
		background-color: #0094ff;
		text-indent: 2em;
		color: #fff;
		height: 40px;
		line-height: 40px;
		font-size: 18px;
	}

	span.custom-select-option { width: 100%; display: block; }
</style>